<template>
  <div class="store-address-selector">
    <treeselect
      v-model="selectedAddresses"
      data-qa="store-address-selector-tree"
      :disabled="disabled"
      :multiple="true"
      :options="storeAddressOptions"
      :flatten-search-results="true"
      value-format="object"
      :placeholder="computedPlaceholder"
      v-on="$listeners"
    />
  </div>
</template>

<script>
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import { mapGetters } from 'vuex'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'

export default {
  name: 'StoreAddressSelector',
  components: { Treeselect },
  props: {
    options: {
      type: Array,
      default: () => []
    },
    selected: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    },
    cityCode: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    forceClearSelection: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      storeAddressIds: [],
      computedSelection: []
    }
  },
  computed: {
    ...mapGetters('authentication', [GET_CURRENT_STORE_ADMIN_GETTER]),
    computedPlaceholder() {
      return this.placeholder || this.$t('order.stores')
    },
    user() {
      return this[GET_CURRENT_STORE_ADMIN_GETTER]
    },
    storeAddressOptions() {
      return this.mapTreeData(this.options)
    },
    selectedAddresses: {
      get() {
        return this.mapTreeData(this.computedSelection)
      },
      set(selectedNodes) {
        this.storeAddressIds = this.getAddressIds(selectedNodes, [])
        this.$emit('selection-change', this.storeAddressIds)
      }
    }
  },
  watch: {
    options() {
      if (this.forceClearSelection) {
        this.computedSelection = []
      }
    }
  },
  mounted() {
    this.computedSelection = this.selected
  },
  methods: {
    mapTreeData(addresses) {
      const { cities } = this.user || {}
      if (!Array.isArray(cities) || !addresses || addresses.length === 0) {
        return []
      }

      const selectedIds = new Set(addresses)
      if (this.cityCode) {
        const city = cities.find(city => city.code === this.cityCode) || {}
        return this.reduceCity(city, selectedIds)
      }
      return this.reduceCities(cities, selectedIds)
    },
    reduceCity(city, storeAddressIds) {
      const { stores } = city || {}
      if (!Array.isArray(stores)) {
        return []
      }
      return this.reduceStores(stores, storeAddressIds)
    },
    reduceCities(cities, storeAddressIds) {
      return cities.reduce((filteredCities, city) => {
        const filteredStores = this.reduceStores(city.stores, storeAddressIds)
        if (filteredStores.length > 0) {
          filteredCities.push({
            id: city.code,
            label: city.name,
            children: filteredStores
          })
        }
        return filteredCities
      }, [])
    },
    reduceStores(stores, storeAddressIds) {
      return stores.reduce((filteredStores, store) => {
        const filteredAddresses = this.reduceAddressesWithIds(
          store.storeAddresses,
          storeAddressIds
        )
        if (filteredAddresses.length > 0) {
          filteredStores.push({
            id: store.name,
            label: store.name,
            children: filteredAddresses
          })
        }
        return filteredStores
      }, [])
    },
    reduceAddressesWithIds(addresses, storeAddressIds) {
      return addresses.reduce((storeAddresses, address) => {
        if (storeAddressIds.has(address.id)) {
          storeAddresses.push({
            id: address.id,
            address: address.address,
            label: address.address
          })
        }
        return storeAddresses
      }, [])
    },
    getAddressIds(nodes) {
      const nodesToCheck = [...nodes]
      const ids = []
      while (nodesToCheck.length > 0) {
        const node = nodesToCheck.pop()
        if (node.address) {
          ids.push(node.id)
        } else {
          node.children.forEach(child => nodesToCheck.push(child))
        }
      }
      return ids
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~styles';

.store-address-selector {
  margin-bottom: spacing(5);
}

.vue-treeselect /deep/ {
  .vue-treeselect__input {
    @include main-text;
    box-sizing: border-box;
  }

  & .vue-treeselect__control {
    min-height: spacing(12);
  }
  & .vue-treeselect__multi-value-item {
    background-color: $dim-grey;
    color: $white;
    font-size: $font-size-body2;
  }
  & .vue-treeselect__multi-value-item:hover {
    color: $yellow;
    font-size: $font-size-body2;
    & .vue-treeselect__icon {
      color: $yellow;
    }
  }
  & .vue-treeselect__placeholder {
    line-height: spacing(12);
  }
  & .vue-treeselect__icon {
    color: $white;
  }
  & .vue-treeselect__value-remove {
    border-left: none;
    padding: 0 5px;
  }
  & .vue-treeselect__checkbox--checked,
  & .vue-treeselect__checkbox--indeterminate {
    border-color: $yellow;
    background: $yellow;
  }
}
</style>
